import { Route, useHistory } from "react-router-dom";
export function PageNotFound() {
  const history = useHistory();
  return (
    <>
      <h4
        style={{
          color: "grey",
          cursor: "pointer",
          textDecoration: "underline"
        }}
        onClick={() => history.push("/recipes")}
      >
        Go to the Recipes
      </h4>
      <Route path="*">
        <img
          onClick={() => history.push("/recipes")}
          alt="404"
          src="data:image/png;base64,"
        />
        <img
          onClick={() => history.push("/recipes")}
          alt="icon"
          src="data:image/png;base64,"
        />
      </Route>
    </>
  );
}
