import React, { useEffect, useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { Snackbar, Switch } from "@material-ui/core";
import { useHistory, useParams } from "react-router-dom";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { Alert } from "@material-ui/lab";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(6),
    padding: theme.spacing(2),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    backgroundColor: theme.palette.background.paper,
    borderRadius: "16px"
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(2)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  }
}));

const RecipeSchema = yup.object().shape({
  headTxt: yup.string().required(),
  txt1: yup.string().required(),
  txt2: yup.string().required(),
  img: yup.string().url().required(),
  amt: yup.number().required(),
  time: yup.number().required(),
  star: yup.number().required()
  //veg: yup.boolean().required()
});

export default function AddRecipe() {
  const classes = useStyles();
  const history = useHistory();
  const [openRecipeAdd, setopenRecipeAdd] = useState(false);
  const [checked, setChecked] = useState(false);
  const [recipeData, setRecipeData] = useState({});

  const {
    register,
    handleSubmit,
    getValues,
    formState: { errors }
  } = useForm({
    resolver: yupResolver(RecipeSchema)
  });

  const addRecipe = (data) => {
    data.veg = checked ? false : true;
    console.log("form data", data);
    setopenRecipeAdd(true);
    fetch("https://60c83b2fafc88600179f660c.mockapi.io/user/recipe-admin", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(data)
    })
      .then((data) => data.json())
      .then((data) => console.log(data))
      .then((data) => {
        setopenRecipeAdd(true);
        history.push("/users");
      });
  };
  useEffect(() => {
    if (rid) {
      loadRecipeDetails(rid);
    }
  }, []);

  function loadRecipeDetails(id) {
    fetch(
      `https://60c83b2fafc88600179f660c.mockapi.io/user/recipe-admin/${id}`,
      {
        method: "GET"
      }
    )
      .then((data) => data.json())
      .then((data) => {
        //console.log("recipe", data);
        setRecipeData(data);
      });
  }

  const editRecipe = (data) => {
    console.log("edit...");
    console.log("form data", data);
    setopenRecipeAdd(true);
    fetch(
      `https://60c83b2fafc88600179f660c.mockapi.io/user/recipe-admin/${rid}`,
      {
        method: "PUT",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(data)
      }
    )
      .then((data) => data.json())
      .then((data) => console.log(data))
      .then((data) => {
        setopenRecipeAdd(true);
        history.push("/users");
      });
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setopenRecipeAdd(false);
  };
  const { rid } = useParams();

  return (
    <>
      <Button
        onClick={() => history.goBack()}
        color="secondary"
        style={{ float: "left" }}
        variant="outlined"
      >
        Back
      </Button>

      <Container component="main" maxWidth="sm">
        <div className={classes.paper}>
          <Typography component="h1" variant="h5">
            {rid ? "Edit" : "Add "} Recipe
          </Typography>
          {recipeData.id !== undefined ? <EditFunction /> : addFunction()}
        </div>
        <Snackbar
          open={openRecipeAdd}
          autoHideDuration={2000}
          onClose={handleClose}
        >
          <Alert onClose={handleClose} severity="success">
            Successfully {rid ? "Edited" : "Added"} {getValues("headTxt")}
          </Alert>
        </Snackbar>
      </Container>
    </>
  );

  function addFunction() {
    return (
      <form className={classes.form} noValidate>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              {...register("headTxt")}
              error={errors?.headTxt?.message} // red color
              helperText={errors?.headTxt?.message}
              label="Recipe Name"
              autoFocus
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Text 1"
              {...register("txt1")}
              error={errors?.txt1?.message}
              helperText={errors?.txt1?.message}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              size="small"
              variant="outlined"
              required
              multiline
              rows="3"
              fullWidth
              label="Text 2"
              {...register("txt2")}
              error={errors?.txt2?.message}
              helperText={errors?.txt2?.message}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Image URL"
              type="url"
              {...register("img")}
              error={errors?.img?.message}
              helperText={errors?.img?.message}
            />
          </Grid>
          <Grid item xs={6} sm={6}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Amount"
              type="number"
              placeholder="Rs."
              {...register("amt")}
              error={errors?.amt?.message}
              helperText={errors?.amt?.message}
            />
          </Grid>
          <Grid item xs={6} sm={6}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Time"
              type="number"
              placeholder="Mins"
              {...register("time")}
              error={errors?.time?.message}
              helperText={errors?.time?.message}
            />
          </Grid>
          <Grid item xs={6} sm={6}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Star"
              type="number"
              placeholder="4"
              {...register("star")}
              error={errors?.star?.message}
              helperText={errors?.star?.message}
            />
          </Grid>
          <Grid item xs={6} sm={6}>
            <TextField
              hidden
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Veg / Non-veg"
              placeholder="true"
              {...register("veg")}
              error={errors?.veg?.message}
              helperText={errors?.veg?.message}
              value={checked ? false : true}
            />
            <FormControlLabel
              control={
                <Switch
                  checked={checked}
                  onChange={() => setChecked(!checked)}
                />
              }
              label="Non-Veg"
            />
          </Grid>
        </Grid>
        <Button
          type="button"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={handleSubmit(addRecipe)}
        >
          Add
        </Button>
      </form>
    );
  }
  function EditFunction() {
    return (
      <form className={classes.form} noValidate>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              {...register("headTxt")}
              error={errors?.headTxt?.message} // red color
              helperText={errors?.headTxt?.message}
              label="Recipe Name"
              autoFocus
              defaultValue={recipeData.id ? recipeData.headTxt : ""}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Text 1"
              {...register("txt1")}
              error={errors?.txt1?.message}
              helperText={errors?.txt1?.message}
              defaultValue={recipeData.txt1}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              size="small"
              variant="outlined"
              required
              multiline
              rows="3"
              fullWidth
              label="Text 2"
              {...register("txt2")}
              error={errors?.txt2?.message}
              helperText={errors?.txt2?.message}
              defaultValue={recipeData.txt2}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Image URL"
              type="url"
              {...register("img")}
              error={errors?.img?.message}
              helperText={errors?.img?.message}
              defaultValue={recipeData.img}
            />
          </Grid>
          <Grid item xs={6} sm={6}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Amount"
              type="number"
              placeholder="Rs."
              {...register("amt")}
              error={errors?.amt?.message}
              helperText={errors?.amt?.message}
              defaultValue={recipeData.amt}
            />
          </Grid>
          <Grid item xs={6} sm={6}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Time"
              type="number"
              placeholder="Mins"
              {...register("time")}
              error={errors?.time?.message}
              helperText={errors?.time?.message}
              defaultValue={recipeData.time}
            />
          </Grid>
          <Grid item xs={6} sm={6}>
            <TextField
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Star"
              type="number"
              placeholder="4"
              {...register("star")}
              error={errors?.star?.message}
              helperText={errors?.star?.message}
              defaultValue={recipeData.star}
            />
          </Grid>
          {/* {console.log(recipeData.veg)} */}
          <Grid item xs={6} sm={6}>
            <TextField
              hidden
              size="small"
              variant="outlined"
              required
              fullWidth
              label="Veg / Non-veg"
              placeholder="true"
              {...register("veg")}
              error={errors?.veg?.message}
              helperText={errors?.veg?.message}
              //value={recipeData.veg ? false : true}

              defaultValue={recipeData.veg}
            />
            <FormControlLabel
              control={
                <Switch
                  checked={!recipeData.veg}
                  onChange={() => setChecked(!recipeData.veg)}
                />
              }
              label="Non-Veg"
            />
          </Grid>
        </Grid>
        <Button
          type="button"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={handleSubmit(rid ? editRecipe : addRecipe)}
        >
          {rid ? "Edit" : "Add +"}
        </Button>
      </form>
    );
  }
}
