import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
//import List from "@material-ui/core/List";
//import ListItem from "@material-ui/core/ListItem";
import {
  List,
  ListItem,
  ListItemAvatar,
  ListItemSecondaryAction,
  ListItemText,
  Snackbar
} from "@material-ui/core";
//import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
//import ListItemText from "@material-ui/core/ListItemText";
//import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import Avatar from "@material-ui/core/Avatar";
import { Button, IconButton, Typography } from "@material-ui/core";
import { Delete } from "@material-ui/icons";
import { useHistory } from "react-router-dom";
import { Alert } from "@material-ui/lab";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    // minWidth: 460,
    // maxWidth: 480,
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(1)
  },
  top: {
    display: "flex",
    justifyContent: "space-between",
    margin: theme.spacing(1),
    background: "whitesmoke",
    padding: theme.spacing(1)
    // minWidth: 460,
    // maxWidth: 480
  },
  large: {
    width: theme.spacing(10),
    height: theme.spacing(10),
    marginLeft: theme.spacing(0.1)
  },
  title: {
    marginLeft: theme.spacing(2)
  }
}));

export default function RecipeList() {
  const history = useHistory();
  const classes = useStyles();
  const [recipes, setRecipes] = useState([]);
  const [openRecipeDelete, setopenRecipeDelete] = useState(false);
  useEffect(() => {
    loadRecipe();
  }, []);
  function loadRecipe() {
    fetch(
      "https://60c83b2fafc88600179f660c.mockapi.io/user/recipe-admin?sortBy=createdAt&order=desc",
      {
        method: "GET"
      }
    )
      .then((data) => data.json())
      .then((recipes) => setRecipes(recipes))
      .catch((e) => console.log(e));
  }
  function deleteRecipe(id) {
    fetch(
      `https://60c83b2fafc88600179f660c.mockapi.io/user/recipe-admin/${id}`,
      {
        method: "DELETE"
      }
    )
      .then((data) => data.json()) // inside 'then' means it is delete sucessfully
      .then((data) => console.log(data))
      .then((data) => setopenRecipeDelete(true))
      .then((data) => loadRecipe());
  }
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setopenRecipeDelete(false);
  };
  return (
    <div style={{ margin: "auto" }}>
      <div className={classes.top}>
        <Button
          onClick={() => history.goBack()}
          color="secondary"
          variant="outlined"
          size="small"
        >
          Back
        </Button>
        <Typography
          component="h6"
          variant="h5"
          color="textPrimary"
          style={{ padding: "8px" }}
        >
          Recipe List - ({recipes.length})
        </Typography>
        <Button
          onClick={() => history.push("/addRecipe")}
          color="primary"
          variant="outlined"
          size="small"
        >
          Add +
        </Button>
      </div>
      <List className={classes.root}>
        {recipes.map((data) => {
          return (
            <ListItem key={data.id} button>
              <ListItemAvatar>
                <Avatar
                  variant="rounded"
                  alt={data.headTxt}
                  src={data.img}
                  className={classes.large}
                />
              </ListItemAvatar>
              <ListItemText
                onClick={() => history.push(`/addRecipe/${data.id}`)}
                primary={
                  <>
                    <Typography
                      component="h5"
                      variant="h5"
                      className={classes.inline}
                      color="textPrimary"
                    >
                      {data.headTxt}
                    </Typography>
                  </>
                }
                secondary={data.txt1}
                className={classes.title}
              />
              <ListItemSecondaryAction>
                <IconButton edge="end" aria-label="delete" color="secondary">
                  <Delete onClick={() => deleteRecipe(data.id)} />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItem>
          );
        })}
        <Snackbar
          open={openRecipeDelete}
          autoHideDuration={2000}
          onClose={handleClose}
        >
          <Alert onClose={handleClose} severity="error">
            Successfully Removed
          </Alert>
        </Snackbar>
      </List>
    </div>
  );
}
