import {
  AppBar,
  Badge,
  Button,
  Fab,
  IconButton,
  Toolbar
} from "@material-ui/core";
import { ExitToApp, Lock, ShoppingCart } from "@material-ui/icons";
import { useHistory } from "react-router-dom";

export function Appbar({ counter, isAdmin, setIsAdmin }) {
  const history = useHistory();
  return (
    <AppBar position="sticky">
      <Toolbar>
        <Button color="inherit" onClick={() => history.push("/")}>
          Home
        </Button>
        <Button color="inherit" onClick={() => history.push("/recipes")}>
          Recipes
        </Button>
        <Button color="inherit" onClick={() => history.push("/cart")}>
          Cart
        </Button>
        <Button color="inherit" onClick={() => history.push("/users")}>
          Users
        </Button>
        <IconButton
          size="large"
          className="App_floatingBar"
          style={{ fontWeight: "bold", float: "right", marginLeft: "auto" }}
        >
          <Fab
            className="App_header_fab"
            onClick={() => setIsAdmin(false)}
            title="Logout"
          >
            {isAdmin ? (
              <Lock style={{ color: "green" }} />
            ) : (
              <ExitToApp style={{ color: "red" }} />
            )}
          </Fab>
        </IconButton>
        <IconButton
          size="large"
          className="App_floatingBar"
          style={{ fontWeight: "bold", float: "right" }}
        >
          <Badge badgeContent={counter} color="primary">
            <Fab className="App_header_fab" title="Cart">
              <ShoppingCart />
            </Fab>
          </Badge>
        </IconButton>
      </Toolbar>
    </AppBar>
  );
}
